*{
  margin: 0px;
  padding: 0px;
}
html{
  background-color: #282c34;
}
.App {
  text-align: center;
  color: white;
  background-color: #282c34;

}

.form-main{
  padding: 15px;
  display: flex;
  flex-direction: column;
}

.inputbox{
  display: flex;
  justify-content: space-evenly;
  margin-bottom: 10px !important;
}

.btn-main{
  width: 15% !important;
  margin: auto !important;
}

.flexbox{
  display: flex;
}

.error{
  color:red;
}

.hide{
  display: none;
}

.red{
  color: red;
}

img{
  width: 100%;
  height: 50%;
  object-fit: cover;
}

.card-deck{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.card{

  width: 23%;
  margin: 10px;
  color: black;
  transition-duration: 0.6s;
  background-color: #dfe1e1;
}


.card:hover{
  cursor: pointer;
  transform: scale(1.04);
  box-shadow: 0px 0px 10px 1px white;
}

.next-prev-btn{
  display: flex;
  padding: 15px;
  justify-content: space-around;
}